/*------------------------------------------------------------------
	[Helpers]
------------------------------------------------------------------*/
/*------------------------------------------------------------------
	[Row]
------------------------------------------------------------------*/

.row-space-2 {
	margin-right: -2px;
	margin-left: -2px;

	> [class*="col-"] {
		padding-left: 2px;
		padding-right: 2px;
	}
}

/*--------------------------------------------------
	[Container Full Width]
----------------------------------------------------*/

.container-full-width {
	margin-right: auto;
	margin-left: auto;
	@include clearfix;
}

/*--------------------------------------------------
	[Container Small]
----------------------------------------------------*/

.container-sm {
	padding-right: 15px;
	padding-left: 15px;
	margin-right: auto;
	margin-left: auto;
	@include clearfix;
}

/* Media Queries below 992px */
@media (min-width: $screen-sm-min) {
	.container-sm {
		width: 750px;
	}
}

/* Media Queries below 1200px */
@media (min-width: $screen-md-min) {
	.container-sm {
		width: 970px;
	}
}


/*------------------------------------------------------------------
	[Content]
------------------------------------------------------------------*/

.content {
	padding-top: 40px;
	padding-bottom: 40px;
}

.content-sm {
	padding-top: 60px;
	padding-bottom: 60px;
}

.content-md {
	padding-top: 80px;
	padding-bottom: 80px;
}

.content-lg {
	padding-top: 100px;
	padding-bottom: 100px;
}


/*------------------------------------------------------------------
	[Full Width]
------------------------------------------------------------------*/

.full-width {
	@include size(100%, auto);
}

