/*------------------------------------------------------------------
  [Variables]
------------------------------------------------------------------*/
/*------------------------------------------------------------------
  [Mixins]
------------------------------------------------------------------*/
/*------------------------------------------------------------------
	[Base]
------------------------------------------------------------------*/
body {
  overflow-x: hidden;
  font-family: "Source Sans Pro", "Helvetica Neue", Helvetica, Arial, sans-serif;
}

.text-muted {
  color: #777777;
}

.text-primary {
  color: #F05F40;
}

p {
  font-size: 14px;
  line-height: 1.75;
}

a {
  color: #F05F40;
}

a:hover,
a:focus,
a:active,
a.active {
  color: #e74c3c;
}

a,
a:hover,
a:focus,
a:active,
a.active {
  outline: none;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Montserrat", "Helvetica Neue", Helvetica, Arial, sans-serif;
  text-transform: uppercase;
  font-weight: 700;
}

.img-centered {
  margin: 0 auto;
}

.bg-light-gray {
  background-color: #eeeeee;
}

.bg-darkest-gray {
  background-color: #222222;
}

blockquote {
  border-left: 3px solid #777777;
  color: #777777;
  font-size: 16px;
}

blockquote .blockquote-heading {
  color: #333333;
  text-transform: capitalize;
  font-size: 18px;
  margin-top: 25px;
}

/*------------------------------------------------------------------
	[Custom Navigation Bar]
------------------------------------------------------------------*/
.navbar-custom {
  background-color: #fff;
}

.navbar-custom .navbar-brand {
  color: #F05F40;
  font-family: "Montserrat", "Helvetica Neue", Helvetica, Arial, sans-serif;
  text-transform: uppercase;
  font-weight: 700;
}

.navbar-custom .navbar-brand:hover, .navbar-custom .navbar-brand:focus, .navbar-custom .navbar-brand:active, .navbar-custom .navbar-brand.active {
  color: #e74c3c;
}

.navbar-custom .navbar-collapse {
  border-color: rgba(255, 255, 255, 0.98);
}

.navbar-custom .navbar-toggle {
  background-color: #fff;
  border-color: #eeeeee;
  font-family: "Montserrat", "Helvetica Neue", Helvetica, Arial, sans-serif;
  text-transform: uppercase;
  color: #222222;
  font-size: 12px;
  font-weight: 700;
}

.navbar-custom .navbar-toggle:hover, .navbar-custom .navbar-toggle:focus {
  background-color: #F05F40;
  color: #fff;
}

.navbar-custom .nav li > a,
.navbar-custom .nav .open .dropdown-menu > li > a,
.navbar-custom .nav .open > a {
  font-family: "Montserrat", "Helvetica Neue", Helvetica, Arial, sans-serif;
  text-transform: uppercase;
  font-weight: 400;
  letter-spacing: 1px;
  color: #333333;
  background-color: transparent;
  border-radius: 5px;
}

.navbar-custom .nav li > a:hover, .navbar-custom .nav li > a:focus,
.navbar-custom .nav .open .dropdown-menu > li > a:hover,
.navbar-custom .nav .open .dropdown-menu > li > a:focus,
.navbar-custom .nav .open > a:hover,
.navbar-custom .nav .open > a:focus {
  outline: none;
  color: white;
  background-color: #F05F40;
}

.navbar-custom .navbar-nav > .active > a {
  border-radius: 5px;
  color: white;
  background-color: #F05F40;
}

.navbar-custom .navbar-nav > .active > a:hover,
.navbar-custom .navbar-nav > .active > a:focus {
  color: white;
  background-color: #F05F40;
}

@media (min-width: 768px) {
  .navbar-custom {
    background-color: transparent;
    padding: 25px 0;
    transition: padding 0.3s;
    border: none;
  }
  .navbar-custom .container-fluid {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
  }
  .navbar-custom .navbar-brand {
    font-size: 2em;
    transition: all 0.3s;
  }
  .navbar-custom .navbar-collapse {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    width: 100%;
  }
  .navbar-custom .nav {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    width: 100%;
  }
  .navbar-custom .nav li a {
    color: #fff;
    transition: border-radius 0.1s;
  }
  .navbar-custom .nav li .dropdown-menu {
    border-color: #F05F40;
    border-radius: 0 0 5px 5px;
    left: 0;
  }
  .navbar-custom .nav .open > a {
    border-radius: 5px 5px 0 0;
  }
  .navbar-custom .nav .open .dropdown-menu > li > a {
    border-radius: 0;
  }
  .navbar-custom .navbar-nav > .active > a {
    border-radius: 3px;
  }
}

@media (min-width: 768px) {
  .navbar-custom.affix {
    background-color: #222222;
    padding: 10px 0;
    transition: transform 0.3s;
  }
  .navbar-custom.affix .navbar-brand {
    font-size: 1.5em;
  }
  .navbar-custom.affix .navbar-nav li a {
    font-size: 12px;
  }
  .navbar-custom.affix.out {
    transform: translate3d(0, -50%, 0);
    padding: 0;
  }
}

@media (min-width: 768px) and (max-width: 991px) {
  .navbar-custom.affix .navbar-collapse .nav > li.hide-sm {
    display: none;
  }
}

/*------------------------------------------------------------------
	[Header]
------------------------------------------------------------------*/
header {
  background-color: #515769;
  background-image: url("../img/header-bg.jpg");
  background-repeat: no-repeat;
  background-attachment: scroll;
  background-position: center center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  background-size: cover;
  -o-background-size: cover;
  text-align: center;
  color: white;
}

header .intro {
  padding-top: 100px;
  padding-bottom: 50px;
}

header .intro .intro-heading {
  font-family: "Montserrat", "Helvetica Neue", Helvetica, Arial, sans-serif;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 30px;
  line-height: 50px;
  margin-bottom: 25px;
}

@media (min-width: 768px) {
  header .intro {
    padding-top: 250px;
    padding-bottom: 200px;
  }
  header .intro .intro-heading {
    font-size: 75px;
    line-height: 75px;
    margin-bottom: 50px;
  }
}

/*------------------------------------------------------------------
	[Global Section Styles]
------------------------------------------------------------------*/
section {
  padding: 50px 0;
}

section .section-heading {
  font-size: 40px;
  margin-top: 0;
  margin-bottom: 15px;
}

section .section-subheading {
  font-size: 16px;
  font-family: "Droid Serif", "Helvetica Neue", Helvetica, Arial, sans-serif;
  text-transform: none;
  font-style: italic;
  font-weight: 400;
  margin-bottom: 75px;
}

section .section-text {
  font-family: "Droid Serif", "Helvetica Neue", Helvetica, Arial, sans-serif;
  margin-bottom: 20px;
  margin-top: 20px;
  font-size: 18px;
}

@media (min-width: 768px) {
  section {
    padding: 100px 0;
  }
}

@media (max-width: 767px) {
  section .section-heading {
    font-size: 28px;
  }
  section .section-text {
    font-size: 14px;
  }
}

/*------------------------------------------------------------------
  [Footer]
------------------------------------------------------------------*/
footer {
  padding: 25px 0;
  text-align: center;
}

footer span.copyright {
  line-height: 40px;
  font-family: "Montserrat", "Helvetica Neue", Helvetica, Arial, sans-serif;
  text-transform: uppercase;
  text-transform: capitalize;
}

/*------------------------------------------------------------------
	[Pricing Section Styles]
------------------------------------------------------------------*/
#pricing .price-ticket {
  width: 300px;
  height: 80px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  position: relative;
  background: #fff;
  border: 1px solid #CCC;
  border-radius: 2px;
  margin: 50px auto;
  padding-left: 15px;
}

#pricing .price-ticket:before, #pricing .price-ticket:after {
  content: '';
  display: block;
  width: 295px;
  height: 70px;
  background: #fff;
  border: 1px solid #CCC;
  border-radius: 2px;
  transform: rotate(2deg);
  position: absolute;
  top: 10px;
  left: 0;
  z-index: -1;
}

#pricing .price-ticket:after {
  height: 75px;
  transform: rotate(-2deg);
  top: 2px;
  z-index: -2;
}

#pricing .price-ticket > span {
  display: block;
  width: 45px;
  background: #222222;
  position: absolute;
  top: 5px;
  left: -15px;
  /* Text */
  color: #fff;
  font-size: 12px;
  padding: 0px 7px;
  text-align: right;
}

#pricing .price-ticket > .info,
#pricing .price-ticket > .price {
  display: inline-block;
  font-size: 16px;
  margin-bottom: 0;
  /* Text */
  color: #222222;
  font-weight: 600;
}

#pricing .price-ticket > .info {
  line-height: 25px;
}

#pricing .price-ticket > .price {
  width: 60px;
  /* Text */
  color: #F05F40;
  text-align: center;
  font-weight: 700;
  padding-right: 15px;
}

/*------------------------------------------------------------------
	[Services Section]
------------------------------------------------------------------*/
#services .service {
  margin-top: 30px;
  margin-bottom: 30px;
}

#services .service-img {
  max-width: 150px;
}

#services .service-heading {
  margin: 15px 0;
  text-transform: none;
}

#services .service-heading a {
  color: #333333;
}

#services .service-list {
  list-style: circle;
  text-align: left;
  margin: 40px 0;
  font-size: 16px;
}

#services .service-list li {
  margin: 10px 0;
}

/*------------------------------------------------------------------
	[Contact Section]
------------------------------------------------------------------*/
#contact {
  position: relative;
  background-color: rgba(0, 0, 0, 0.6);
}

#contact::after {
  content: "";
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  background-image: url("../img/header-bg.jpg");
  background-position: center;
  background-repeat: no-repeat;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  background-size: cover;
  -o-background-size: cover;
  opacity: 0.6;
  z-index: -1;
}

#contact .section-heading {
  color: #fff;
}

#contact .form-group {
  margin-bottom: 25px;
}

#contact .form-group textarea.form-control {
  height: 236px;
  min-width: 100%;
  max-width: 100%;
  min-height: 230px;
}

#contact .form-control:focus {
  border-color: #F05F40;
  box-shadow: none;
}

#contact ::-webkit-input-placeholder {
  font-family: "Montserrat", "Helvetica Neue", Helvetica, Arial, sans-serif;
  text-transform: uppercase;
  text-transform: initial;
  color: #777777;
}

#contact :-moz-placeholder {
  /* Firefox 18- */
  font-family: "Montserrat", "Helvetica Neue", Helvetica, Arial, sans-serif;
  text-transform: uppercase;
  text-transform: initial;
  color: #777777;
}

#contact ::-moz-placeholder {
  /* Firefox 19+ */
  font-family: "Montserrat", "Helvetica Neue", Helvetica, Arial, sans-serif;
  text-transform: uppercase;
  text-transform: initial;
  color: #777777;
}

#contact :-ms-input-placeholder {
  font-family: "Montserrat", "Helvetica Neue", Helvetica, Arial, sans-serif;
  text-transform: uppercase;
  text-transform: initial;
  color: #777777;
}

#contact .contact-info p {
  font-size: 14px;
  line-height: 1.2;
}

/*------------------------------------------------------------------
	[Button]
------------------------------------------------------------------*/
.btn {
  border-radius: 300px;
}

.btn-primary, .btn-secondary {
  font-family: "Montserrat", "Helvetica Neue", Helvetica, Arial, sans-serif;
  text-transform: uppercase;
  font-weight: 700;
  transition: all 0.35s ease 0s, transform 1s cubic-bezier(0.6, 0.2, 0.1, 1) 0.2s, opacity 1s cubic-bezier(0.6, 0.2, 0.1, 1) 0.2s;
}

.btn-primary {
  color: white;
  background-color: #F05F40;
  border-color: #F05F40;
}

.btn-primary:focus, .btn-primary.focus {
  color: white;
  background-color: #eb3812;
  border-color: #a4270d;
}

.btn-primary:hover {
  color: white;
  background-color: #eb3812;
  border-color: #e13612;
}

.btn-primary:active, .btn-primary.active,
.open > .btn-primary.dropdown-toggle {
  color: white;
  background-color: #eb3812;
  border-color: #e13612;
}

.btn-primary:active:hover, .btn-primary:active:focus, .btn-primary:active.focus, .btn-primary.active:hover, .btn-primary.active:focus, .btn-primary.active.focus,
.open > .btn-primary.dropdown-toggle:hover,
.open > .btn-primary.dropdown-toggle:focus,
.open > .btn-primary.dropdown-toggle.focus {
  color: white;
  background-color: #c93110;
  border-color: #a4270d;
}

.btn-primary:active, .btn-primary.active,
.open > .btn-primary.dropdown-toggle {
  background-image: none;
}

.btn-primary.disabled:hover, .btn-primary.disabled:focus, .btn-primary.disabled.focus, .btn-primary[disabled]:hover, .btn-primary[disabled]:focus, .btn-primary[disabled].focus,
fieldset[disabled] .btn-primary:hover,
fieldset[disabled] .btn-primary:focus,
fieldset[disabled] .btn-primary.focus {
  background-color: #F05F40;
  border-color: #F05F40;
}

.btn-primary .badge {
  color: #F05F40;
  background-color: white;
}

.btn-secondary {
  color: #222222;
  background-color: white;
  border-color: white;
}

.btn-secondary:focus, .btn-secondary.focus {
  color: #222222;
  background-color: #e6e6e6;
  border-color: #bfbfbf;
}

.btn-secondary:hover {
  color: #222222;
  background-color: #e6e6e6;
  border-color: #e0e0e0;
}

.btn-secondary:active, .btn-secondary.active,
.open > .btn-secondary.dropdown-toggle {
  color: #222222;
  background-color: #e6e6e6;
  border-color: #e0e0e0;
}

.btn-secondary:active:hover, .btn-secondary:active:focus, .btn-secondary:active.focus, .btn-secondary.active:hover, .btn-secondary.active:focus, .btn-secondary.active.focus,
.open > .btn-secondary.dropdown-toggle:hover,
.open > .btn-secondary.dropdown-toggle:focus,
.open > .btn-secondary.dropdown-toggle.focus {
  color: #222222;
  background-color: #d4d4d4;
  border-color: #bfbfbf;
}

.btn-secondary:active, .btn-secondary.active,
.open > .btn-secondary.dropdown-toggle {
  background-image: none;
}

.btn-secondary.disabled:hover, .btn-secondary.disabled:focus, .btn-secondary.disabled.focus, .btn-secondary[disabled]:hover, .btn-secondary[disabled]:focus, .btn-secondary[disabled].focus,
fieldset[disabled] .btn-secondary:hover,
fieldset[disabled] .btn-secondary:focus,
fieldset[disabled] .btn-secondary.focus {
  background-color: white;
  border-color: white;
}

.btn-secondary .badge {
  color: white;
  background-color: #222222;
}

.btn-md {
  padding: 10px 16px;
}

.btn-xl {
  font-size: 18px;
  padding: 20px 40px;
}

.btn:focus,
.btn:active,
.btn.active,
.btn:active:focus {
  outline: none;
}

ul.social-buttons {
  margin-bottom: 0;
}

ul.social-buttons li a {
  display: block;
  background-color: #222222;
  height: 40px;
  width: 40px;
  border-radius: 100%;
  font-size: 20px;
  line-height: 40px;
  color: white;
  outline: none;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  transition: all 0.3s;
}

ul.social-buttons li a:hover, ul.social-buttons li a:focus, ul.social-buttons li a:active {
  background-color: #F05F40;
}

@media (max-width: 767px) {
  .btn-md {
    font-size: 12px;
    font-weight: 400;
    padding: 10px 14px;
  }
  .btn-xl {
    font-size: 14px;
    font-weight: 400;
    padding: 12px 18px;
  }
}

